import React from 'react';
import {CHANGE_FIND_TEXT} from '../constants/actions'
import {useDispatch,useSelector} from 'react-redux'


export function FindList(){
  const dispatch = useDispatch()
  const findText = useSelector(state=>state.filteredList.findText)
  const filteredList  = useSelector(state=>state.filteredList.findText===""?([]):state.filteredList.list.reduce((arr,el)=>{

    el.region.includes(state.filteredList.findText) && arr.push(el.region)

    return arr

  },[]) )

  const onclickHandler = (e)=>{
    
    if (e.target.textContent){
      dispatch({type:CHANGE_FIND_TEXT,payload:e.target.textContent.trim()})
    }

  }
  return(
    <div className="FindList" onClick={onclickHandler}>
      {
        (filteredList.length===1 && filteredList[0]===findText)?(null):(
          filteredList.map((el,index)=><div className="FindList__el" key={index}>{el}</div>)
        )

        
      }
    </div>
  )
}